.modal {
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  pointer-events: none;
  transition: 0.5s;
}

.modal.active {
  opacity: 1;
  pointer-events: all;
  z-index: 2;
}

.modalWindow {
  padding: 20px;
  border-radius: 12px;
  background-color: white;
  width: 50vh;
  transform: scale(0.5);
  transition: 0.4s all;
}

.modalWindow.active {
  transform: scale(1);
  display: flex;
  flex-direction: column;
}

@media (max-width: 430px){
  .modal {
    height: 100vh;
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
  }
  
  .modalWindow {
    padding: 20px;
    border-radius: 12px;
    background-color: white;
    width: 50vh;
    transform: scale(0.5);
    transition: 0.4s all;
  }

}
